<template>
	<div>
		<server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
		<identify-card-page
				v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"
		/>
		<face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
		<contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
		<input-driver-chip v-if="currentStep === steps.findIndex(step=>step.key === 'inputDriverChip')" :nextFunction="getDriverChip"/>
		<process
				:type="1"
				v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
				:handle-business="handleBusiness"
		/>
		<input-car v-if="currentStep === steps.findIndex(step=>step.key === 'choiceCar')" :nextFunction="getCarAllInfo" :lastInput="true"/>
<!--		<record-list v-if="currentStep === steps.findIndex(step=>step.key === 'recordList')" :originList="illegalityElectronicMonitorList" :nextFunction="listFunction"/>-->
		<record-list v-if="currentStep === steps.findIndex(step=>step.key === 'recordList')" :isCheck="false" :originList="illegalityElectronicMonitorList" :nextFunction="listFunction"/>

<!--		<notify v-if="currentStep === steps.findIndex(step=>step.key === 'notify')" :selectMonitor="illegalityElectronicSelectMonitor"/>-->
<!--		<sign v-if="currentStep === steps.findIndex(step=>step.key === 'sign')" :selectMonitor="illegalityElectronicSelectMonitor" :nextFunction="signFunction"/>-->

		<process
				:type="2"
				v-if="currentStep === steps.findIndex(step=>step.key === 'sign')"
				:handle-business="signFunction"
		/>

		<pay-qr-健康multiple
				v-if="currentStep === steps.findIndex(step=>step.key === 'payQrMultiple')"
				:handle-complete="handleComplete"
				:returnPrintInfo="getPrintInfo"
				:payUrls="payUrls"
				:payData="illegalityElectronicSelectMonitor.recordArr"
		/>

		<complete
				v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
				:data="completeResult"
				foot-texts="温馨提示：已处理的违法行为若未完成缴费，请至罚款缴纳业务查询！"
		/>
	</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import recordList from '../../components/record-list';
// import notify from './components/notify';
// import sign from './components/sign';
import inputCar from '@/view/pageComponents/input-car';
import inputDriverChip from '@/view/pageComponents/input-driver-chip';
import payQrMultiple from '../../components/pay-qr-multiple';
import complete from '@/components/complete';
import process from '@/components/process';
import { illegalityElectronicStart, illegalityElectronicFace, illegalityElectronicComplute, illegalityElectronicSaveMonitorList, illegalityElectronicCheckMonitor, checkHandleQualifications, illegalityElectronicHandle } from '@/service/traffic/traffic';
import { cloneDeep } from 'lodash';
// import { cancelPrefix } from '@/utils/tools';
import { getStreamUrl } from '@/utils';
import { produceModelReq } from '@/service/common';
import moment from 'moment';
import { ILLEGAL_ELECTRONIC_CHECK_YINCHUAN, ILLEGAL_ELECTRONIC_YINCHUAN } from '@/constant/stepsOption';
import { illegalityPrint } from '@/utils/printInfo';
import { tickPrintAll } from '@/service/HardwareRequest';

export default {
    name: 'illegality-electronic-maanshan',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        recordList,
        inputCar,
        inputDriverChip,
        complete,
        payQrMultiple,
        process
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            userBusinessId: 'userBusinessId',
            identifyCard: 'identifyCard',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        })
    },
    data () {
        return {
            hphm: '',
            hpzl: '02',
            clsbdh: '',
            chip: '',
            illegalityElectronicMonitorList: [],
            illegalityElectronicSelectMonitor: { recordArr: [] },
            completeResult: {},
            payUrls: []
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage', 'setPrintInfo']),
        async handleBusiness () {
            const res = await illegalityElectronicStart({
                idNum: this.identifyCard.certNumber,
                zxbh: this.chip
            });
            if (res && res.code === '0') {
                if (res.result.status === 1) {
                    this.handleContrast();
                } else {
                    this.changeTipPage({ message: res.message });
                }
            } else {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleContrast () {
            const res = await illegalityElectronicFace({
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleComplete () {
            const res = await illegalityElectronicComplute({
                payStatus: 1,
                monitorRecordIds: this.illegalityElectronicSelectMonitor.recordArr.map((item) => {
                    return item.monitorRecordId;
                })
            });
            if (res && res.code === '0') {
                this.completeResult = res.result;
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        getDriverChip (chip) {
            this.chip = chip;
            this.changeStep('down');
        },
        async getCarAllInfo (params) {
            this.hphm = params.carLicense;
            this.hpzl = params.carType;
            this.clsbdh = params.clsbdm;

            const res = await illegalityElectronicCheckMonitor({ ...params, zxbh: this.chip });
            if (res && res.code === '0') {
                if (res.result.code === 1) {
                    this.illegalityElectronicMonitorList = res.result.data;
                    await this.checkState();
                } else {
                    this.changeTipPage({ message: res.message ? res.message : res.result.msg, enableBack: true });
                }
            } else {
                this.changeTipPage({ message: res.message ? res.message : res.result.msg, enableBack: true });
            }
        },
        async checkState () {
            const res = await checkHandleQualifications({
                plateType: this.hpzl,
                plateNumber: this.CONSTANT.carTypeName + this.hphm
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async listFunction ({ selection, totalFines, totalScore }) {
            const res = await illegalityElectronicSaveMonitorList({
                ...selection[0],
                monitorRecordIds: selection.map((item) => {
                    return item.monitorRecordId;
                })
            });
            if (res && res.code === '0') {
                const list = cloneDeep(res.result);
                delete res.result.monitorProcessingRecordDTOS;
                delete res.result.monitorRecordIds;
                delete res.result.status;

                this.illegalityElectronicSelectMonitor = {
                    holder: res.result.holder,
                    mobile: res.result.mobile,
                    licenceNo: res.result.licenceNo,
                    plateNumber: res.result.plateNumber,
                    recordArr: list.monitorProcessingRecordDTOS.map((item) => {
                        const dataMatch = selection.find((i) => {
                            return i.jkbh === item.jkbh;
                        });
                        if (dataMatch) {
                            return { ...dataMatch, ...item, ...res.result };
                        } else {
                            return { ...item, ...res.result };
                        }
                    }),
                    totalFines: totalFines,
                    totalScore: totalScore
                };

                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async signFunction (img) {
            const list = this.illegalityElectronicSelectMonitor.recordArr;

            const res = await illegalityElectronicHandle({
                // usersign: cancelPrefix(img),
                monitorRecordIds: list.map((item) => {
                    return item.monitorRecordId;
                })
            });
            if (res && res.code === '0') {
                let recordArr = cloneDeep(this.illegalityElectronicSelectMonitor.recordArr);
                // 更新决定书编号
                recordArr = recordArr.map((item) => {
                    const i = res.result.find((x) => {
                        return x.monitorRecordId === item.monitorRecordId;
                    });

                    if (i) {
                        const obj = { ...item, ...i, ...i.monitorProcessingRecordDTO };
                        obj.illegalTimeFormat = moment(obj.illegalTime).format('YYYY年MM月DD日HH时mm分');
                        obj.year = moment().format('YYYY');
                        obj.month = moment().format('MM');
                        obj.day = moment().format('DD');
                        delete obj.monitorProcessingRecordDTO;
                        return obj;
                    }
                });
                this.illegalityElectronicSelectMonitor.recordArr = recordArr;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async getBook () {
            const list = this.illegalityElectronicSelectMonitor.recordArr;
            const data = {
                code: 'cfjds',
                body: {
                    list: list,
                    handWriterImg: getStreamUrl(this.userBusinessId)
                }
            };
            data.direction = 1;
            const res = await produceModelReq(data);
            if (res && res.code === '0') {
                this.setPrintInfo(res.result);// 预览所需图片Id
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async getPrintInfo (printInfos) {
            for (let i = 0; i < printInfos.length; i++) {
                await tickPrintAll(illegalityPrint(printInfos[i]));
            }
        }
    },
    mounted () {
        // 查询业务无支付
        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.illegalityElectronic) {
            this.setSteps(ILLEGAL_ELECTRONIC_YINCHUAN);
        } else {
            this.setSteps(ILLEGAL_ELECTRONIC_CHECK_YINCHUAN);
        }
    }
};

</script>
