export const illegalityPrint = (params) => {
    const info = {
        payList: [
            { bold: false, bigText: true, payload: 'PrintFormatText', content: params.title },
            { bold: false, bigText: false, payload: 'PrintFormatText', content: '编号：' + params.jdsbh },
            { bold: false, bigText: false, payload: 'PrintFormatText', content: params.baseInfo },
            { bold: false, bigText: false, payload: 'PrintFormatText', content: params.content },
            { payload: 'PrintImage', url: params.url },
            { bold: false, bigText: false, payload: 'PrintFormatText', content: params.bottom },
            { bold: false, bigText: false, payload: 'PrintFormatText', content: params.payToolTip },
            { payload: 'PrintQRCodeImage', url: params.payQrCode },
            { payload: 'Feed', n: '100' },
            { payload: 'CutPaper' }
        ]
    };
    console.log('处罚决定书打印：', JSON.stringify(info));
    return info;
};
